<template>
  <v-container
    id="regular-forms"
    fluid
    tag="section"
  >
    <v-col cols="12">
      <base-material-card
        color="primary"
        inline
        icon="mdi-mail"
        title="Mail"
        class="px-5 py-3"
      >
        <v-form
          ref="form"
          lazy-validation
        >
          <v-container class="py-0">
            <crud ref="crud" />

            <v-btn
              color="primary"
              @click="submit"
            >
              Update
            </v-btn>
          </v-container>
        </v-form>
      </base-material-card>
    </v-col>
  </v-container>
</template>

<script>
  import Crud from './Crud'

  export default {
    name: 'Edit',
    components: { Crud },
    data: () => {
      return {
        loaded: false,
      }
    },
    mounted: function () {
      this.$http.get(`/portal/organization/${this.$route.params.id}`)
        .then(
          (response) => {
            this.$refs.crud.api = response.data
          }
        )
        .catch(
          () => {
            this.$router.push({ name: 'organization' })
          }
        )
    },
    methods: {
      submit () {
        if (!this.$refs.form.validate()) {
          return
        }

        let apiData = this.$refs.crud.api
        this.$http.post(
          `/portal/organization/${apiData.id}`,
          JSON.stringify({
            name: apiData.name,
            email: apiData.email,
            from_email: apiData.from_email,
            fo_api_code: apiData.fo_api_code,
            payment_type: apiData.payment_type,
            sms_sender_name: apiData.sms_sender_name,
            max_transaction_amount: apiData.max_transaction_amount,
            phone_verification_method: apiData.phone_verification_method,
          })
        )
          .then((response) => {
            this.$toasted.global.success(response.data.message)
            this.$router.push({ name: 'organization' })
          })
          .catch((error) => {
            this.$toasted.global.error(error.response.data.message)
          })
      },
    },
  }
</script>

